/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
  color: inherit;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "fot-tsukuardgothic-std", "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  letter-spacing: 0.08em;
  font-weight: bold;
  color: #292929;
}

a {
  transition: opacity 0.5s;
}

a:hover,
a:focus {
  opacity: 0.8;
}

footer {
  height: 100px;
}

.globalNav {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(100%);
  transition: all 0.3s ease;
}

.globalNav_item {
  color: #2ca9b6;
  margin-bottom: 45px;
}

.globalNav_item:last-of-type {
  margin-bottom: 50px;
}

.is-navActive .globalNav {
  transform: translateX(0);
}

.hamburgerButton {
  width: 24px;
  height: 18px;
  transition: all 0.3s ease-in-out;
  position: relative;
  z-index: 1;
}

.hamburgerButton__line {
  position: absolute;
  top: 0;
  right: 0;
  margin: auto;
  width: 24px;
  height: 2px;
  background-color: #60d8cc;
  transition: inherit;
}

.hamburgerButton__line::after, .hamburgerButton__line::before {
  content: "";
  position: absolute;
  display: block;
  height: 100%;
  background-color: inherit;
  transition: inherit;
}

.hamburgerButton__line::before {
  width: 20px;
  top: 6px;
  right: 0;
}

.hamburgerButton__line::after {
  width: 16px;
  top: 12px;
  right: 0;
}

.is-navActive .hamburgerButton__line {
  background-color: transparent;
}

.is-navActive .hamburgerButton__line::before,
.is-navActive .hamburgerButton__line::after {
  top: 9px;
  background-color: #60d8cc;
  width: 24px;
}

.is-navActive .hamburgerButton__line::before {
  transform: rotate(45deg);
}

.is-navActive .hamburgerButton__line::after {
  transform: rotate(-45deg);
}

.header {
  position: fixed;
  width: 100%;
  height: 50px;
  z-index: 1;
  background-color: #fff;
  padding: 0 20px;
}

.header_wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_title {
  display: flex;
  align-items: flex-end;
}

.header_logo {
  display: block;
  width: 110px;
  margin-right: 10px;
}

.header_text {
  display: block;
  font-size: 12px;
  letter-spacing: 0.1em;
}

.mv {
  width: 100%;
  height: 600px;
}

.mv_wrapper {
  width: 100%;
  height: 100%;
  padding: 0 20px 20px;
  position: relative;
}

.mv_imgContainer {
  width: 100%;
  height: 100%;
}

.mv_imgContainer img {
  display: block;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: 70%;
}

.mv_catchContainer {
  width: 89vw;
  position: absolute;
  bottom: 8.3%;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  text-shadow: 0 0 20px rgba(21, 21, 21, 0.2);
}

.mv_catchEn {
  text-align: center;
  font-family: "Caveat", cursive;
  font-size: 16vw;
  letter-spacing: -0.01em;
  font-feature-settings: "palt";
}

.mv_catchEn::after {
  content: "";
  display: block;
  width: 75vw;
  margin: 4px auto 8px;
  height: 3px;
  background-color: #fff;
  transform: rotate(-2deg);
  box-shadow: 0 0 20px rgba(21, 21, 21, 0.2);
}

.mv_catchJp {
  font-size: 4.8vw;
  letter-spacing: 0.1em;
  text-align: right;
  font-style: italic;
  font-feature-settings: "palt";
  margin-right: 16px;
}

.u-entryButton {
  display: block;
  width: 200px;
  height: 46px;
  border-radius: 23px;
  color: #fff;
  background: linear-gradient(to right, #24a2b3, #68e0cf);
  text-align: center;
  line-height: 46px;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 0.12em;
  position: relative;
}

.u-entryButton img {
  display: block;
  width: 25px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.u-sectionTitle {
  font-size: 100px;
  font-family: "Caveat", cursive;
  letter-spacing: 0;
  text-transform: uppercase;
}

.u-sectionTitle._01 {
  color: #fff;
}

.u-sectionTitle._02 {
  color: #60d8cc;
}

.u-sectionTitle._03 {
  color: #eefbfa;
}

.u-sectionSubtitle {
  font-size: 24px;
  margin-top: -46px;
  font-style: italic;
}

.u-visuallyHidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
}

body {
  overflow-x: hidden;
}

.members_img {
  width: 60vw;
}
